.home-outer-container {
    width: 100vw;
    height: 100h;
    display: flex;
	flex-direction: row;

}

.home-inner-container {
    margin-left: 10vw;
    margin-right: 10vw;
    height: 80vh;
    width: 100vw;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
}


.text{
    text-align: center;
    font-size: 330%;
    display: flex;
    flex-direction: column;
    transition: 0.4s;
}

.text:hover {
    transform: scale(1.05);
    
}